import React, { lazy, memo, Suspense, useMemo } from "react";
import { shallowEqual, useSelector } from "react-redux";
import { Route, Switch, useRouteMatch } from "react-router";
import styled from "styled-components";
import useRedirect from "../../common/hooks/useRedirect";
import Nav from "./Nav";
const PdfManagement = lazy(() => import("./PdfManagement"));
const UserInfoManagement = lazy(() => import("./UserInfoManagement"));
const VideoManagement = lazy(() => import("./VideoManagement"));

const UserManagement = ({ className }) => {
  const { path: parentPath } = useRouteMatch();
  const isActive = useSelector(
    (state) => state.userStatus?.userInfo?.user?.role?.is_active,
    shallowEqual
  );
  useRedirect(useMemo(() => [{ condition: isActive, to: "/getting-report" }], [isActive]));
  const route = [
    { path: "/pdf", component: PdfManagement, name: "File PDF", exact: true },
    { path: "/video", component: VideoManagement, name: "Xem video của bạn", exact: true },
    { path: "", component: UserInfoManagement, name: "Thông tin cá nhân", exact: false },
  ];
  return (
    <main
      className={`${className} container user__management mt-3 mt-lg-5 flex-column flex-md-row flex-grow-1`}
    >
      <Nav route={route} />
      <Suspense
        fallback={
          <div style={{ width: "60%" }} id="loader-wrapper">
            <div id="loader"></div>
          </div>
        }
      >
        <Switch>
          {route.map(({ path, component, name, exact }) => (
            <Route key={name} path={`${parentPath}${path}`} component={component} exact={exact} />
          ))}
        </Switch>
      </Suspense>
    </main>
  );
};

export default memo(styled(UserManagement)`
  display: flex;
  width: 100%;
`);
