import React from "react";
import { useRouteMatch } from "react-router";
import { NavLink } from "react-router-dom";
import styled from "styled-components";

const Nav = ({ route, className }) => {
  const { url } = useRouteMatch();
  return (
    <div
      className={`${className} pe-0 pe-xl-5 pe-lg-4 pe-md-3 pe-sm-2 mb-3 mb-lg-0   col-12 col-md-2`}
    >
      <div className="avatar justify-content-center d-none d-md-flex mx-auto mb-3">
        <img src="https://www.sohoceravietnam.com/public/img/meow.jpg" alt="avatar" />
      </div>
      <nav className={`user__management__navigation d-flex flex-row flex-md-column`}>
        {route.reverse().map(({ path, name }) => (
          <NavLink
            exact
            to={`${url}${path}`}
            key={name}
            activeClassName="active"
            className="fw-bold"
          >
            {name}
          </NavLink>
        ))}
      </nav>
    </div>
  );
};

export default styled(Nav)`
  .avatar {
    position: relative;
    width: 80%;
    padding-top: 80%;
    img {
      /* $size: 100px; */
      position: absolute;
      top: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      /* width: 200px; */
      /* height: 200px; */
      border-radius: 50%;
    }
  }
  .user__management {
    &__navigation {
      column-gap: 16px; /* margin: 0 32px 0 0; */
      a {
        /* margin: 0 20px 0 0; */
        padding: 10px 0;
      }
      .active {
        color: ${(props) => props.theme.colorTextTitle};
        border-bottom: 2px solid ${(props) => props.theme.colorSecondary};
      }
    }
  }
`;
